.data {
  width: 70vw;
  text-align: center;
  margin: 0% auto;
  position: relative;
}
.close {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 140%;
  transition: 0.3s;
  cursor: pointer;
}
.close:hover {
  color: rgba(255, 0, 0, 0.753);
}
.image {
  max-width: 120%;
}
.loaderImage {
  z-index: -10;
}
.getData {
  margin: 20% auto 0% auto;
  color: #dadadaf5;
}
.getData button {
  color: #dadadaf5;
  background-color: rgb(65, 65, 65);
  outline: none;
  border: none;
  border-radius: 20px;
  padding: 0.7%;
  transition: 0.5s;
  padding: 0.7%;
}
.getData button:hover {
  background-color: grey;
}

.imageAndAbout {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: row;
  margin-top: 5%;
}

.imageDiv,
.explanationDiv {
  width: 50%;
  margin-top: 1%;
}
.explanationDiv {
  color: #dadadaf5;
  margin-right: 3%;
}
.imageDiv {
  margin-left: 3%;
}
.explanation {
  line-height: 125%;
  font-size: 115%;
  color: rgba(0, 0, 0, 0.733);
}
.title {
  color: #dadadac9;
  font-weight: 700;
  color: rgba(0, 0, 0, 0.733);
}
.date {
  color: #dadadaf5;
  font-size: 130%;
  color: rgba(0, 0, 0, 0.733);
}
.about {
  font-size: 220%;
  color: #fffffff5;
}
.btn {
  margin: 2% auto;
  padding: 2%;
}
.inputDiv {
  color: #dadadaf5;
  margin-right: 3%;
  padding: 0.7%;
  outline: none;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  transition: 0.5s;
  background-color: rgb(65, 65, 65);
  transition: 0.5s;
}
.inputDiv:hover {
  background-color: grey;
}
.warning {
  color: rgb(68, 0, 0);
  font-size: 120%;
  font-weight: bold;
}
@media screen and (max-width: 700px) {
  .getData {
    margin: 40% auto 0% auto;
    color: #dadadaf5;
  }
  .dateAndButton {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .imageAndAbout {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    margin-top: 5%;
  }
  .imageDiv,
  .explanationDiv {
    width: 100%;
  }
  .image {
    width: 100%;
  }
  .explanation {
    line-height: 110%;
    font-size: 105%;
    color: rgba(0, 0, 0, 0.733);
  }
  .data {
    padding: 2%;
  }
  .about {
    font-size: 170%;
  }
  .inputDiv {
    padding: 2%;
  }
  .btn {
    padding: 2%;
  }
}
@media screen and (max-width: 1400px) {
  .getData {
    margin: 20% auto 0% auto;
    color: #dadadaf5;
    font-size: 130%;
  }
  .dateAndButton {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .inputDiv {
    padding: 2%;
  }
  .btn {
    padding: 2%;
  }
}
